/*
  ====================================================================
  Header
  ====================================================================
*/
.loader{
    min-height: 750px;
    //min-height: 640px;
    background-color: $black;
    &__btn{
        //width: 55px;
        margin: 0 auto;
        width: 100%;
        z-index: 1;
        position: absolute;
        top: 75%;
        opacity: 0;
        .btn{
            background: $main;
            display: block;
            width: 33px;
            height: 33px;
            margin: 0 auto;
            outline: 0;
            border-radius: 100%;
            border: 3px solid $main;
            transition: $transition;
            &:hover{
                transition: $transition;
                transform: scale(1.2);
                &:before{
                    opacity: 0;
                    transition: $transition;
                }
            }
            &:before {
                content: '';
                position: absolute;
                width: 52px;
                height: 52px;
                background-color: transparent;
                border: 2px solid $main;
                border-radius: 100%;
                opacity: 0.2;
                z-index: 4;
                margin: -12px 0 0 -12px;
                &:hover{
                    //transform: scale(1.1);
                    display: none;
                    width: 0;
                    height: 0;
                }
            }
        }
        .icon {
            display: inline-block;
            width: 4em;
            height: 1em;
            margin: 12px 10px;
            background: no-repeat;
            background-image: url(../img/content/svg/arrow_drpdown.svg), none ; /* Modern browsers */
            background-size: auto 100%;
            vertical-align: middle;
            font-size: 8px;
        }
    }
}
.garkushyn{
    margin: 0 auto;
    width: 100%;
    position: absolute;
    top: 35%;
    text-align: center;
    span{
        font-size: 90px;
        color: $main;
        font-family: $Bold;
        position: relative;
        transition: $transition;
        opacity: 0;
        &:nth-child(1){
            //position: relative;
            //right: 10px;
            //top: 50px;
        }
    }
}

@media only screen and (max-width: 650px) {
    .loader {
        //min-height: 400px;
    }
    .garkushyn {
        span {
            font-size: 40px;

        }
    }
}
