.portfolio{
    background: $black;
    &__above{
        //background: red;
        overflow: hidden;
    }
    &__item{
        width: 33.33%;
        float: left;
    }
    &__title{
        //opacity: 0;
        transform: scale(1.3);
        position: absolute;
        z-index: 2;


    }
    h2{
        color: transparent;
        font-size: 20px;
        position: absolute;
        left: 40px;
        top: 30px;
        transform: scale(1.8);
        transition: $transition;

        //display: none;
    }
    span {
        padding-bottom: 3px;
        position: relative;
        top: 10px;
    }
    a:hover{
        h2{
            color: $white;
            transition: $transition;
            transform: scale(1);

        }
        span {
            border-bottom: 3px solid $main;
        }
    }


    img{
        width: 100%;
        overflow: hidden;
        z-index: 2;
        position: relative;
        transition: $transition;
        transform: scale(1);
        &:hover{
            transition: $transition;
            opacity: 0.3;
            transform: scale(0.99);
        }
    }
    video{
        width: 100%;
        height: 90%;
        margin-bottom: -6px;
        z-index: 3;
        transition: $transition;
        transform: scale(1);
        h2{
            color: transparent;
            transform: scale(0.98);
            transition: $transition;
        }

        &:hover{
            transition: $transition;
            opacity: 0.3;
            //transform: scale(1.05) translate3d(0,0,0);

        }

    }
    .ciklum{
        margin-top: -3px;
    }
    &__below{
        //background: green;
        margin-top: -3px;


        .one{
            float: left;
            width: 33.3%;
        }
        .two{
            float: left;
            width: 66.7%;

        }
    }
    .row__top{
        overflow: hidden;

    }
    .row__bottom{
        overflow: hidden;
        margin-top: -2px;
    }
    .giga{
        margin-top: -16%;
    }
    @media only screen and (max-width: 500px) {
        img{
            margin-top: -5px;
        }
        .portfolio {
            &__item {
                width: 100%;
                float: none;
                margin-top: -5px;
            }
            img{
                margin-top: -5px;
            }
            &__below{
                //background: green;
                margin-top: -3px;
                .one{
                    width: 100%;
                    float: none;
                }
                .two{
                    width: 100%;
                    float: none;
                }
            }
            .giga{
                margin-top: 0;
            }
        }
    }
}
